import { saveProductToCookie } from "./common/function";
import { ProductSidebar, ProductWithSb, RecentlyViewed, RelatedProduct } from "./common/section";

(() => {
  theme.sectionRegister.forEach((item) => {
    switch (item) {
      case "product-template":
        register(item, ProductWithSb);
        break;
      case "product-sidebar":
        register(item, ProductSidebar);
        break;
      case "related-product":
        register(item, RelatedProduct);
        break;
      case "recently-viewed":
        register(item, RecentlyViewed);
        break;
    }
    load("*");
  });
  saveProductToCookie();
  console.log("product-with-sb-page.js loaded");
})();
